.cat-main {
  /* background-color: rgb(159, 242, 242); */
  height: 90vh;
  display: flex;
  flex-direction: row-reverse;
  /* border: 2px solid red; */
}

.c-main-right {
  border-left: 2px solid red;
  margin: auto;
  width: 70%;
}
.bars {
  margin: auto;
  height: 50%;
  display: flex;
  flex-direction: column;
  /* border: 2px solid red; */
  width: 50%;
  text-align: center;
  align-items: center;
}
.bars .links {
  width: 100%;
  margin: 12px;
  display: flex;
  flex-direction: row;
}
.bars .links .c-left-buttons {
  /* border: 3px solid red; */
  padding: 4px;
  width: 20%;
  margin: 0vh auto;
  color: black;
  /* margin-left: 0px; */
  font-size: 1rem;
  text-align: center;
  border-radius: 30px;
  background-color: rgb(200, 198, 198);
}
.c-main-left {
  margin: auto;
  /* border: 2px solid red; */
  width: 100%;
  text-align: center;
  align-items: center;
}

.button {
  width: 100%;
}
.c-table {
  width: 70%;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
  margin: 4vh auto;
}

.c-main-left .create {
  align-items: end;
  margin-right: 10vh;
}

.main-product {
  height: 100vh;
  padding: 4vh;
  display: flex;
}
.right-product {
  height: 100%;
  padding: 12px;
  display: flex;
  flex-direction:column;
  align-items: center;
  justify-content: center;
  
  width: 50%;
  /* border-bottom: 2px solid red; */
  margin: auto;
}
.right-product .form {
  padding: 4vh 0vh;
  width: 50% !important;
  margin: auto;
  /* background-color: red; */
  box-shadow: rgba(17, 12, 46, 0.15) 0px 48px 100px 0px;

}
.left-product {
  /* padding: 12vh; */
  border: 1px solid red;
  /* width: 100%; */
}
.product-form {
  width: 90%;

  margin: auto;
}
.product-form label {
  width: 30%;
}
.main-products {
  height: 85vh;
}
@media only screen and (max-width: 700px) {
  .main-product {
    display: flex;
    flex-direction: column;
  }
  .left-product {
    padding: 12vh;
    border: 1px solid red;
    width: 100%;
  }
  .right-product {
    width: 100%;
    /* border: 2px solid red; */
  }
  .cat-main {
    /* height: 100%; */
    display: flex;
    flex-direction: column-reverse;
  }
  .c-main-left {
    padding-top: 3vh;
    display: flex;
    flex-direction: column;
    width: 100%;
    margin: auto;
    /* border: 2px solid red; */
  }
  .cat-box {
    margin-left: 2vw;
    /* border: 2px solid red; */
  }
  .c-main-left .create {
    /* border: 2px solid black; */
    width: 100%;
    margin: auto;
  }
  .c-main-right {

    width: 100%;
  }
  .c-main-right .c-table {
    width: 100%;
    padding: 15vh !important;
    background-color: rgb(255, 255, 255);
  }
  .bars {
    display: flex;
    justify-content: space-between;
    border-bottom: 2px solid rgb(0, 0, 0);
    width: 100%;
  /* padding-top: 12vh; */
    margin: auto;
    max-width: 400px;
  }
  .bars .links {
    width: 74%;

    margin: auto;
    display: flex;
    flex-direction: row;
  }
  .bars .links .c-left-buttons {
    /* border: 3px solid red; */
    padding: 4px;
    width: 50% !important;
    color: black;
    margin: 0px 4px;
    
    font-size: 0.7rem;
    text-align: center;
    border-radius: 10px;
    background-color: rgb(200, 198, 198);
  }
}

