/* ---------------contact------------- */
.contact-card {
  display: flex;
}
.contact-main {
  height: 90vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.contact-top {
  justify-content: center;
  display: flex;
  flex-direction: row;
  width: 100%;
  /* border: 2px solid red; */
  /* justify-content: center; */
}
.contact-card {
  background-color: #ffffff;
  width: 20%;
  border: 2px solid rgb(23, 25, 25);
  padding: 2vh;
  border-radius: 30px;
  margin-right: 12px;
  margin-top: 12px;
  display: flex;
  flex-direction: column;
}

/* contact-form */
.contact-form {
  background-color: white;
  width: 60%;
  padding: 4vh;
  margin: 12vh auto;
  border: 2px solid rgb(141, 112, 112);
}

.contact-bottom {
  width: 70%;
}
.contact-form-label {
  color: black;
}
.contact-background {
  background: url("../../public/image/bull.jpg");
  background-position: 0%;
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Do not repeat the image */
  background-size: cover;
  padding: 2vh;
}

