  }
}

/*   -------------home page css -------------------------*/
.Home-section {
  height: calc(100% - 50px);
  background-color: white;
}
.w-banner {
  margin: auto;
  width: 65%;
  height: 150px;
  /* border: 21px solid red; */
}
.w-banner img {
  height: 100%;
  width: 100%;
}
.second-section {
  box-shadow: 2px 2px 2px 2px black;
  display: flex;
  padding-top: 14px;
  flex-direction: row;
}
.right-sec {
  width: 80%;
  /* border: 2px solid red; */
}
.home-products {
  justify-content: center;
  width: 100%;
  /* margin: 3px; */
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.home-card img {
  /* border: 2px solid black; */
  border-radius: 30px;
  padding: 3px;
  height: 150px;
  width: 100%;
  margin: auto;
}
.home-card img:hover {
  /* border: 2px solid black; */
  border-radius: 30px;
  padding: 10px;
  /* height: 100px; */
  /* width: 100%; */
  /* margin: auto; */
}
.home-card-body {
  /* margin-left: 2vh; */

  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: start;
}

.home-card {
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  margin: 33px;
  border-radius: 30px;
  /* border: 2px solid black; */
  width: 30vh;
  padding: 8px;
  margin-top: 20px !important;
}

.left-sec {
  margin: 2vh;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  /* color: #cccccc !important; */
  /* border-right: 2px solid rgb(30, 24, 24); */
  width: 20%;
  /* border: 2px solid red !important; */
}
.h-filters {
  margin: 0vh auto;
  width: 100%;
  /* border: 2px solid red; */
  display: flex;
  flex-direction: column;
}
.t-filter {
  width: 50%;
  margin: 12px auto;
}
.b-filter {
  width: 80%;
  margin: 12px auto;
}
.home-card-body .button button {
  width: 70px;
  font-size: 1rem;
  text-align: center;
}
.home-card-body h6 button {
  text-align: center;
}
