.right-category .card-list {
  margin: 4vh;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}
.right-category .card-list .card {
  width: 27vh;
  margin: 2vh;
  height: 40vh;
}
.right-category .card-list .card img {
  height: 50%;
  width: 100%;
}
.admin-product {
  display: flex;
  flex-direction: row;
}

@media only screen and (max-width: 700px) {
  .admin-product {
    display: flex;
    flex-direction: column;
  }
  /* --------------create-product-------------- */
  .main-product {
    height: 100%;
  }
  .right-product .form {
    padding: 12px;
    width: 100% !important;
    max-width: 400px;
    /* background-color: red; */
    box-shadow: rgba(17, 12, 46, 0.15) 0px 48px 100px 0px;
  }
  .right-product {
    margin-top: 12vh;
    width: 100%;
    /* border: 2px solid red; */
    margin: auto;
    /* border: 2px solid red; */
    margin-top: 6vw;
  }
  .right-product div {
    width: 100%;
  }
}
.a-card {
  /* border: 1px solid black; */
  margin: 2vh;
  width: 30vh;
}
.a-card img {
  height: 70%;
  width: 100%;
  padding: 2px;
}

.a-card .card-body h3 {
  font-size: 1.5rem;
  border-top: 1px solid rgb(45, 39, 39);
  width: 100%;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}

.a-card .card-body p {
  font-size: 0.9rem;
}
.a-card .card-body p h5 {
  font-size: 0.5rem;
}


.update-product{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}